import React from 'react'
import { MainLayout } from './MainLayout'
import Shape1 from '../assets/img/Combined_Shape_Copy.png'
import Shape2 from '../assets/img/Shape2.png'
import Shape3 from '../assets/img/Shape3.png'
import Shape4 from '../assets/img/Shape4.png'
import Shape5 from '../assets/img/Shape5.png'

export default function Hero() {
    return (
        <MainLayout>
            <section className='hero'>
                <div className="grid-container-hero">
                    <div className='title'>
                        Find the
                        best<span>talent</span>
                    </div>
                    <div className="desc">
                        <div className="rectangle">
                        </div>
                        <div className='desc-txt'>
                            Finding the right people and building high performing teams can be hard. Most companies aren’t tapping into the abundance of global talent. We’re about to change that.
                        </div>
                    </div>
                </div>
                <img className='shape1' src={Shape1}/>
                <img className='shape2'  src={Shape2}/>
                <img className='shape3'  src={Shape3}/>
                <img  className='shape4' src={Shape4}/>
                <img  className='shape5' src={Shape5}/>
            </section>
        </MainLayout >
    )
}
