import React from 'react'
import Shape5 from '../assets/img/Shape5.png'
import Shape2 from '../assets/img/Combined_Shape8.png'
import Shape7 from '../assets/img/Shape7.png'
import Shape6 from '../assets/img/Rectangle2.png'

export function GetStarted() {
    return (
        <div className="get-started-section">
            <div className="get-started-container">
                <div className='title'>Ready to get started?</div>
                <button><span>contact us</span></button>
               
            </div>
            <img className='shape5' src={Shape5} />
                <img className='shape2' src={Shape2} />
                <img className='shape6' src={Shape6} />
                <img className='shape7' src={Shape7} />
        </div>
    )
}
